form {
    width: 50%;
    box-shadow: 0 0 10px rgb(199, 199, 199);
    margin: 30px auto;
    padding: 40px;
    border-radius: 5px;
    /* background-color: #eee; */
}

@media(max-width:768px) {
    form {
        width: 97%;
    }
}

form h1 {
    margin-bottom: 20px;
}

form input,
textarea {
    display: block;
    padding: 10px 15px;
    outline: 0;
    border: 1px solid #ddd;
    background-color: white;
    font-size: 19px;
    width: 100%;
}

form label input {
    width: 20px;
}

form input:not(:last-of-type) {
    margin-bottom: 15px;
}

form input[type='submit'] {
    cursor: pointer;
    background-color: rgb(11, 108, 227);
    color: white;
    transition: 0.3s;
}

form input[type='submit']:active {
    transform: scale(0.96);
}

form p {
    /* padding: 10px 0; */
    margin-top: 19px;
}

form p a {
    color: rgb(7, 139, 221);
    font-weight: bold;
}